exports.init = (initLink) => {
    let req = new XMLHttpRequest();
    req.open("GET",initLink,false);
    req.send(null);
    if (req.status === 200) {
        let response = null;
        try {
            response = JSON.parse(req.responseText)
        }catch (e) {
            return null;
        }
        if (response.status === undefined) {
            return null;
        }else if (response.status !== '0') {
            return null;
        }else {
            return response.authID
        }
    }
};
